<template>
    <div>
        <header class="page-header">
            <div class="page-title"></div>
        </header>
        <div class="mainframe-content">
            <div class="content-block">
                <header class="table-edit-header">
                    <div class="display-flex">
                        <div class="font-16 font-bold flex">
                            <span></span>
                        </div>
                        <div class="button-item">
                            <el-button type="primary" size="small" @click="onShowAdd"></el-button>
                        </div>
                    </div>
                </header>
                <el-table :data="dataList" @selection-change="onSelectionChange">
                    <el-table-column width="45" type="selection"/>
                    <el-table-column prop="id" width="80" label="ID"/>
                    <el-table-column prop="title" label=""/>
                    <el-table-column prop="price" label="" width="100"/>
                    <el-table-column prop="iap_price" label="" width="100"/>
                    <el-table-column prop="iap_id" label="ID" width="100"/>
                    <el-table-column prop="type" label="" width="50" align="center"/>
                    <el-table-column prop="day_fee" label="" width="80" align="center"/>
                    <el-table-column width="100" label="" align="center">
                        <template slot-scope="scope">
                            <i class="el-icon-check font-18" v-if="scope.row.enable"/>
                            <i class="el-icon-close font-18" v-else/>
                        </template>
                    </el-table-column>
                    <el-table-column width="50" label="" align="right">
                        <template slot-scope="scope">
                            <a @click="onShowEdit(scope.row)"></a>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="table-edit-footer">
                    <el-button size="small" type="primary" :disabled="selectionIds.length===0" @click="onBatchDelete">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({enable:1})">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({enable:0})">
                        
                    </el-button>
                    <div class="flex"></div>
                </div>
            </div>
        </div>
        <el-dialog title="" closeable :visible.sync="showDialog" :close-on-click-modal="false"
                   :close-on-press-escape="false">
            <table class="dsxui-formtable">
                <colgroup>
                    <col style="width: 80px;">
                    <col style="width: 200px;">
                    <col>
                </colgroup>
                <tbody>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.title"/>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-select class="w-100" v-model="product.type">
                            <el-option :value="1" label=""/>
                            <el-option :value="2" label=""/>
                            <el-option :value="3" label=""/>
                            <el-option :value="4" label=""/>
                            <el-option :value="5" label=""/>
                            <el-option :value="6" label=""/>
                        </el-select>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.price"/>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.original_price"/>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.iap_price"/>
                    </td>
                    <td class="cell-tips">appstore connect</td>
                </tr>
                <tr>
                    <td class="cell-label">ID</td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.iap_id"/>
                    </td>
                    <td class="cell-tips">appstore connect</td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="product.day_fee"/>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                </tbody>
                <tfoot>
                <tr>
                    <td></td>
                    <td>
                        <el-button type="primary" size="medium" class="w100" @click="onSubmit"></el-button>
                    </td>
                    <td></td>
                </tr>
                </tfoot>
            </table>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "Product",
    data() {
        return {
            type: 0,
            product: {},
            dataList: [],
            selectionIds: [],
            showDialog: false,
        }
    },
    methods: {
        fetchList() {
            this.$get('/openai/product.getList').then(response => {
                this.dataList = response.result.items;
            });
        },
        resetData() {
            this.product = {
                type: 1,
                iap_enable: 1
            };
        },
        onSubmit() {
            let {product} = this;
            if (!product.title) {
                this.$showToast('');
                return false;
            }
            if (!product.price) {
                this.$showToast('');
                return false;
            }
            this.$post('/openai/product.save', {product}).then(() => {
                this.showDialog = false;
                this.fetchList();
            });
        },
        onShowAdd() {
            this.resetData();
            this.showDialog = true;
        },
        onShowEdit(d) {
            this.product = d;
            this.showDialog = true;
        },
        onSelectionChange(val) {
            this.selectionIds = val;
        },
        onBatchUpdate(data) {
            let ids = this.selectionIds.map((d) => d.id);
            this.$post('/openai/product.batchUpdate', {ids, data}).then(() => {
                this.fetchList();
            });
        },
        onBatchDelete() {
            let ids = this.selectionIds.map((d) => d.id);
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$post('/openai/product.batchDelete', {ids}).then(() => {
                    this.fetchList();
                });
            });
        },
        onClickTab(tab) {
            this.type = tab.name;
            this.fetchList();
        }
    },
    mounted() {
        this.fetchList();
    },
}
</script>

