<template>
    <div>
        <header class="page-header">
            <div class="page-title"></div>
        </header>

        <div class="mainframe-content">
            <div class="content-block">
                <div class="dsxui-form-inline">
                    <div class="form-item">
                        <div class="form-item-label">UID</div>
                        <div class="form-item-input">
                            <el-input size="medium" class="w200" clearable v-model="params.uid"></el-input>
                        </div>
                    </div>
                    <div class="form-item">
                        <div class="form-item-label"></div>
                        <div class="form-item-input">
                            <el-input size="medium" class="w200" clearable v-model="params.nickname"></el-input>
                        </div>
                    </div>
                    <div class="form-item">
                        <div class="form-item-label"></div>
                        <div class="form-item-input">
                            <el-input size="medium" class="w200" clearable v-model="params.phone"></el-input>
                        </div>
                    </div>
                    <div class="form-item">
                        <div class="form-item-label"></div>
                        <div class="form-item-input">
                            <el-input size="medium" class="w200" clearable v-model="params.email"></el-input>
                        </div>
                    </div>
                    <div class="form-item">
                        <el-button size="medium" type="primary" @click="onSearch"></el-button>
                    </div>
                </div>
            </div>

            <div class="content-block">
                <div class="table-edit-header">
                    <el-tabs @tab-click="onClickTab" value="all">
                        <el-tab-pane label="" name="all"></el-tab-pane>
                        <el-tab-pane label="" name="1"></el-tab-pane>
                        <el-tab-pane label="" name="0"></el-tab-pane>
                        <el-tab-pane label="" name="-1"></el-tab-pane>
                    </el-tabs>
                    <div class="buttons-wrapper">
                        <router-link to="/user/edit" target="_blank">
                            <el-button type="primary" size="small"></el-button>
                        </router-link>
                    </div>
                </div>
                <el-table :data="dataList" v-loading="loading" @selection-change="onSelectionChange">
                    <el-table-column width="45" type="selection"/>
                    <el-table-column prop="uid" width="80" label="UID"/>
                    <el-table-column label="" width="70">
                        <template slot-scope="scope">
                            <el-image :src="scope.row.avatar" class="img-40" fit="cover"></el-image>
                        </template>
                    </el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <a>{{scope.row.nickname}}</a>
                        </template>
                    </el-table-column>
                    <el-table-column prop="phone" label=""/>
                    <el-table-column prop="group.name" width="120" label=""/>
                    <el-table-column prop="credits" width="120" label=""/>
                    <el-table-column prop="created_at" width="170" label=""/>
                    <el-table-column width="100px" label="" align="right">
                        <template slot-scope="scope">
                            <router-link :to="'/user/edit/'+scope.row.uid" target="_blank"></router-link>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="table-edit-footer">
                    <el-button size="small" type="primary" :disabled="selectionIds.length===0" @click="onDelete">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({state:1})">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({freeze:1})">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({freeze:0})">
                        
                    </el-button>
                    <div class="flex"></div>
                    <el-pagination
                            background
                            layout="prev, pager, next, total"
                            :total="total"
                            :page-size="pageSize"
                            :current-page="page"
                            @current-change="onPageChange"
                    >
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PaginationMixin from "../mixins/PaginationMixin";

    export default {
        name: "UserList",
        mixins: [PaginationMixin],
        data() {
            return {
                params: {
                    uid: '',
                    nickname: '',
                    status: '',
                    email: '',
                    phone: ''
                },
                amount: 10000,
                showDialog: false,
                user: {},
                listApi: '/user/user.getList'
            }
        },
        methods: {
            onDelete() {
                let ids = this.selectionIds.map((d) => d.uid);
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$post('/user/user.batchDelete', {ids}).then(() => {
                        this.fetchList();
                    });
                });
            },
            onClickTab(tab) {
                this.params.status = tab.name;
                this.onSearch();
            },
            onBatchUpdate(data) {
                let ids = this.selectionIds.map((d) => d.uid);
                this.$post('/user/user.batchUpdate', {ids, data}).then(() => {
                    this.fetchList();
                });
            },
        },
        mounted() {
            this.fetchList();
        },
    }
</script>

