<script>
    import ImagePicker from "../../lib/ImagePicker";

    export default {
        name: "SettingsPoster",
        components: {
            ImagePicker,
        },
        props: {
            settings: Object
        },
        data() {
            return {
                showPicker: false,
                key: ''
            }
        },
        methods: {
            onChooseImage(data) {
                this.settings[this.key] = data.image;
            },
            showImagePicker(key) {
                this.key = key;
                this.showPicker = true;
            }
        }
    }
</script>

