<template>
    <div>
        <div class="map-header">
            <el-input v-model="address" placeholder="">
                <el-button slot="append" icon="el-icon-search" @click="onSearch"></el-button>
            </el-input>
        </div>
        <div class="map-contaner">
            <div id="tmap"></div>
            <span class="iconfont icon-location-fill marker"></span>
        </div>
        <div class="map-footer">
            <div class="flex-fill flex-column justify-content-center">
                <div>{{position.longitude}}, :{{position.latitude}}</div>
                <div>{{value}}</div>
            </div>
            <div>
                <el-button type="primary" size="small" @click="onConfirm"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "LocationMapView",
        props: {
            location: {
                type: Object,
                default: () => ({
                    latitude: 26.568278689590727,
                    longitude: 104.88300627668536
                })
            },
            zoom: {
                type: Number,
                default: 15
            }
        },
        data() {
            return {
                map: null,
                value: '',
                address: '',
                position: {
                    latitude: 0,
                    longitude: 0
                }
            }
        },
        methods: {
            initMap() {
                let id = document.getElementById('tmap');
                if (!id) return;
                //
                let {latitude, longitude} = this.location;
                let center = new TMap.LatLng(latitude, longitude);
                //map TMap.Map() 
                let map = new TMap.Map(id, {
                    center: center,//
                    zoom: this.zoom,   //
                    pitch: 43.5,  //
                    rotation: 45    //
                });

                map.on('dragend', () => {
                    let {lat, lng} = this.map.getCenter();
                    let location = lat + ',' + lng;
                    this.getLocation({location});
                });
                this.map = map;

                // if (navigator.geolocation) {
                //     navigator.geolocation.getCurrentPosition((position) => {
                //         map.setCenter(position.coords)
                //     }, positionError => {
                //         console.log(positionError);
                //     });
                // } else {
                //     console.log('');
                // }
            },
            getLocation(params) {
                this.$get('/lbs/geocoder', params).then(response => {
                    let {address_component} = response.result;
                    let {adcode} = response.result.ad_info;
                    let {lat, lng} = response.result.location;
                    let {province, city, district, street_number} = address_component;
                    this.value = province + city + district + street_number;
                    this.position = {
                        ...address_component,
                        longitude: lng,
                        latitude: lat,
                        adcode
                    };
                });
            },
            onSearch() {
                let {address} = this;
                if (address) this.getLocation({address});
            },
            geoLocation() {
                let geolocation = new qq.maps.Geolocation();
                geolocation.getLocation((result) => {
                    let {lat, lng} = result;
                    let location = lat + ',' + lng;
                    this.getLocation({location});
                    this.map.setCenter(new TMap.LatLng(lat, lng));
                }, e => {
                    this.getLocation();
                    this.$showToast(':' + e.message + '(' + e.code + ')');
                    //this.$showToast('');
                });
            },
            onConfirm() {
                this.$emit('confirm', this.position);
            }
        },
        mounted() {
            this.initMap();
            this.geoLocation();
        },
    }
</script>

<style lang="scss" scoped>
    .map-header {
        display: flex;
        margin-bottom: 10px;
    }

    .map-footer {
        margin-top: 10px;
        display: flex;
        font-size: 12px;
    }

    .map-contaner {
        position: relative;

        .marker {
            position: absolute;
            z-index: 100;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -100%);
            font-size: 30px;
            color: #f00;
        }
    }

    #tmap {
        height: 450px;
        width: 100%;
        display: block;
        flex: 1;
    }
</style>
