<template>
    <div>
        <header class="page-header">
            <div class="page-title"></div>
        </header>
        <div class="mainframe-content">
            <div class="content-block">
                <header class="table-edit-header">
                    <div class="display-flex">
                        <div class="font-16 font-bold flex">
                            <span></span>
                        </div>
                        <div class="button-item">
                            <el-button type="primary" size="small" @click="onShowAdd"></el-button>
                        </div>
                    </div>
                </header>
                <el-table :data="dataList">
                    <el-table-column prop="cate_id" label="ID" width="50"/>
                    <el-table-column prop="cate_name" label=""/>
                    <el-table-column prop="sort_num" width="200" label=""/>
                    <el-table-column width="80" label="" align="center">
                        <template slot-scope="scope">
                            <i class="el-icon-check font-18" v-if="scope.row.enable"/>
                            <i class="el-icon-close font-18" v-else/>
                        </template>
                    </el-table-column>
                    <el-table-column width="100" label="" align="right">
                        <template slot-scope="scope">
                            <a @click="onShowEdit(scope.row)"></a> |
                            <a @click="onDelete(scope.row.cate_id)"></a>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <el-dialog title="" closeable :visible.sync="showDialog" :close-on-click-modal="false"
                   :close-on-press-escape="false">
            <table class="dsxui-formtable">
                <colgroup>
                    <col style="width: 80px;">
                    <col style="width: 300px;">
                    <col>
                </colgroup>
                <tbody>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="category.cate_name"/>
                    </td>
                    <td></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="category.sort_num"/>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-radio-group v-model="category.enable">
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="0"></el-radio>
                        </el-radio-group>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                </tbody>
                <tfoot>
                <tr>
                    <td></td>
                    <td>
                        <el-button type="primary" size="medium" class="w100" @click="onSave"></el-button>
                    </td>
                    <td></td>
                </tr>
                </tfoot>
            </table>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "PromptCategory",
    data() {
        return {
            dataList: [],
            category: {},
            showDialog: false,
            selectionIds: []
        }
    },
    mounted() {
        this.fetchList();
    },
    methods: {
        fetchList() {
            this.$get('/openai/prompt.category.getList').then(response => {
                this.dataList = response.result.items;
            });
        },
        resetData() {
            this.category = {sort_num: 0};
        },
        onDelete(cate_id) {
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$post('/openai/prompt.category.delete', {cate_id}).then(response => {
                    this.fetchList();
                });
            });
        },
        onSave() {
            let {category} = this;
            if (!category.cate_name) {
                this.$showToast('');
                return false;
            }
            this.$post('/openai/prompt.category.save', {category}).then(response => {
                this.fetchList();
                this.showDialog = false;
            });
        },
        onShowAdd() {
            this.resetData();
            this.showDialog = true;
        },
        onShowEdit(d) {
            this.category = d;
            this.showDialog = true;
        },
    }
}
</script>

