<template>
    <div>
        <header class="page-header">
            <div class="page-title"></div>
        </header>
        <div class="mainframe-content">
            <div class="content-block">
                <header class="table-edit-header">
                    <div class="display-flex">
                        <div class="font-16 font-bold flex">
                            <span></span>
                        </div>
                        <div class="button-item">
                            <el-button type="primary" size="small" @click="onShowAdd"></el-button>
                        </div>
                    </div>
                </header>
                <el-table :data="dataList" @selection-change="onSelectionChange">
                    <el-table-column width="45" type="selection"/>
                    <el-table-column prop="id" width="80" label="ID"/>
                    <el-table-column prop="title" label=""/>
                    <el-table-column width="100" label="">
                        <template slot-scope="scope">
                            <span v-if="scope.row.state === 1"></span>
                            <span style="color: #ff0000;" v-else></span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="created_at" label="" width="170"/>
                    <el-table-column width="50" label="">
                        <template slot-scope="scope">
                            <a @click="onShowEdit(scope.row)"></a>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="table-edit-footer">
                    <el-button size="small" type="primary" :disabled="selectionIds.length===0" @click="onDelete">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({state:1})">
                        
                    </el-button>
                    <el-button size="small" :disabled="selectionIds.length===0" @click="onBatchUpdate({state:0})">
                        
                    </el-button>
                    <div class="flex"></div>
                    <el-pagination
                            background
                            layout="prev, pager, next, total"
                            :total="total"
                            :page-size="pageSize"
                            :current-page="page"
                            @current-change="onPageChange"
                    >
                    </el-pagination>
                </div>
            </div>
        </div>
        <el-dialog title="" closeable :visible.sync="showDialog" :close-on-click-modal="false"
                   :close-on-press-escape="false">
            <table class="dsxui-formtable">
                <colgroup>
                    <col style="width: 80px;">
                    <col style="width: 400px;">
                    <col>
                </colgroup>
                <tbody>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input size="medium" v-model="label.title"></el-input>
                    </td>
                    <td class="cell-tips"></td>
                </tr>
                <tr>
                    <td class="cell-label"></td>
                    <td class="cell-input">
                        <el-input type="textarea" rows="6" v-model="label.content"></el-input>
                    </td>
                    <td class="cell-tips">HTML</td>
                </tr>
                </tbody>
                <tfoot>
                <tr>
                    <td></td>
                    <td>
                        <el-button type="primary" size="medium" class="w100" @click="onSubmit"></el-button>
                    </td>
                    <td></td>
                </tr>
                </tfoot>
            </table>
        </el-dialog>
    </div>
</template>

<script>
    import PaginationMixin from "../mixins/PaginationMixin";

    export default {
        name: "LabelList",
        mixins: [PaginationMixin],
        data() {
            return {
                label: {},
                showDialog: false,
                listApi: '/common/label.getList'
            }
        },
        methods: {
            resetData() {
                this.label = {};
            },
            onDelete() {
                let ids = this.selectionIds.map((d) => d.id);
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.$post('/common/label.batchDelete', {ids}).then(response => {
                        this.fetchList();
                    });
                });
            },
            onSubmit() {
                let {label} = this;
                let {id} = label;
                if (!label.title) {
                    this.$showToast('');
                    return false;
                }
                if (!label.content) {
                    this.$showToast('');
                    return false;
                }

                this.$post('/common/label.save', {id, label}).then(() => {
                    this.showDialog = false;
                    this.resetData();
                    this.fetchList();
                });
            },
            onShowAdd() {
                this.resetData();
                this.showDialog = true;
            },
            onShowEdit(d) {
                this.id = d.id;
                this.label = d;
                this.showDialog = true;
            },
            onSelectionChange(val) {
                this.selectionIds = val;
            },
            onBatchUpdate(data) {
                let ids = this.selectionIds.map((d) => d.id);
                this.$post('/common/label.batchUpdate', {ids, data}).then(response => {
                    this.fetchList();
                });
            }
        },
        mounted() {
            this.fetchList();
        },
    }
</script>

